* {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: #191919;
}
 
#navbar {
    padding: 20px;
    background: #191919 !important;
    border-bottom: 2px solid rgb(225 219 219);
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 10;
}

#nav-items {
    justify-content: flex-end;
}
.container {
    width: 100vw;
    padding: 10px;
    min-height: 80vh;
    color: #fff !important;
    display: flex;
    justify-content: space-between;
}

.container {
    display: flex !important;
}

.left-subcontainer {
    padding: 10px;
}

#prashant-image {
    border-radius: 0%;
    border-bottom: 4px solid #fff;
}

.right-subcontainer {
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: #fff;
}

#name-head {
    font-size: 45px;
    color: rgb(150, 3, 3);
}

#name-end {
    border-bottom: 2px solid rgb(218, 3, 3);
    position: relative;
    left: 40px;
}

.description {
    padding: 10px;
    transition: all ease 0.3s;
}

.hire {
    border-radius: 20px;
    border: 1px solid #c84b31;
    width: 180px;
    height: 40px;
    cursor: pointer;
    background: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
}

.hire:hover {
    background: rgb(13, 129, 206);
    color: #fff;
}

.hire a {
    text-decoration: none;
    border-radius: 25px;
    font-size: 30px;
    color: rgb(250, 15, 15);
}

.hire a:hover {
    color: #fff;
}

@media screen and (max-width: 991px) {
    .container {
        flex-direction: column;
    }
    .left-subcontainer {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    #prashant-image {
        width: 60vw;
    }
}


.main
{
display: flex;
}
.main .text
{
color: #fff;
font-size: 60px;
font-weight: 400;
}
.main .option
{
/* margin-left: 15px; */
height: 90px;
line-height: 90px;
overflow: hidden;
}
.option li
{
list-style: none;
color: #face30;
font-size: 60px;
font-weight: 500;
position: relative;
top: 0;
-webkit-animation: slide 12s steps(4) infinite;
        animation: slide 12s steps(4) infinite;
}
@-webkit-keyframes slide
{
100%
{
top: -360px;
}
}
@keyframes slide
{
100%
{
top: -360px;
}
}
.option li span
{
position: relative;
margin: 5px 0;
line-height: 90px;
}
.option li span::after
{
content: '';
position: absolute;
left: 0;
height: 107%;
width: 100%;
background: #191919;
border-left: 2px solid #face30;
-webkit-animation: typing 3s steps(10) infinite;
        animation: typing 3s steps(10) infinite;
}
@-webkit-keyframes typing
{
40%, 60%
{
left: calc(100% + 30px);
}
100%
{
left: 0;
}
}
@keyframes typing
{
40%, 60%
{
left: calc(100% + 30px);
}
100%
{
left: 0;
}
}
#containerAbout {
